import React, { useState, useEffect } from 'react';
import "./CardItem.scss";
import Counter from "modules/commons/components/counter/Counter";
import VariantsModal from "../variants-modal/VariantsModal";
import { Dropdown } from 'reactjs-dropdown-component';
import { ROUTES } from 'utils/cdn-assets-links';

const CardItem = (props) => {
	const [openModal, setOpenModal] = useState(false);
	const [productMostWanted, setProductMostWanted] = useState(false);
	const [productGroupedGift, setProductGroupedGift] = useState(false);
	const [currentRecommendProduct, setCurrentRecommendProduct] = useState(props.data)

	// useEffect(() => {
	// 	if (openModal) {
	// 		setTimeout(() => {
	// 			document.body.style.overflow = openModal ? 'hidden' : 'unset';
	// 		}, 1000);
	// 	}
	// 	else {
	// 		document.body.style.overflow = 'unset';
	// 	}
	// }, [openModal]);

	const onCardClick = () => {
		if (props.activeVariant && props.activeVariant.activeSizeVariant && props.activeVariant.activeSizeVariant.size) {
			props.getAllVarient()
		}
		setOpenModal(true);
	}

	const setMostWanted = async () => {
		await setProductMostWanted(!productMostWanted)
	}

	const setGroupedGift = async () => {
		await setProductGroupedGift(!productGroupedGift)
	}

	const closeCardModal = () => {
		setOpenModal(false);
	}

	const productDataUpdate = (data) => {
		setCurrentRecommendProduct(data)
	}

	const addProductToRegistry = async () => {
		let data = {}
		if (props.data.isMaItem) {
			data = {
				...props.data,
				mostWanted: productMostWanted ? productMostWanted : false,
				isGroupGift: productGroupedGift ? productGroupedGift : false,
				isCashAcceptedOnly: false
			}
		}
		else {
			data = {
				...currentRecommendProduct,
				mostWanted: productMostWanted ? productMostWanted : false,
				isGroupGift: productGroupedGift ? productGroupedGift : false,
			}
		}
		await props.addProductHandler(data)
		setOpenModal(false)
	}

	const onImgLoadError = (ev, imageUrl) => {
		ev.target.onerror = null;
		ev.target.src = ROUTES.DEFAULT_VARIANT_PATTARN;
	}

	const resetThenSet = (id, key) => {
		props.setActiveVariantSize(id)
	}

	return (
		<>
			{!props.data.isMaItem || (props.data.isMaItem && props.data.maItemVariantItemRecommendation.length > 0) ?
				<>
					<div className={["card-item", props.className ? props.className : ""].join(" ")}>
						<div className="product-image text-center pointer px-2 pt-2" onClick={() => onCardClick()}>
							<img className="mx-auto" src={props.activeVariant ? props.activeVariant.imageUrl : props.data.imageUrl} alt={props.data.title} />
						</div>
						<div className="product-bottom mt-3 px-2 pb-2">
							<div className={props.onCardClick ? "product-title font-medium text-body pointer" : "product-title font-medium text-body"} onClick={() => onCardClick()}>
								{props.data.title}
							</div>

							<div className={props.onCardClick ? "product-sub-text-heading mt-3 text-sm font-medium pointer" : "product-sub-text-heading mt-3 text-sm font-medium"} onClick={() => onCardClick()} >
								<span>Why we love it?</span>
							</div>

							<div className={props.onCardClick ? "product-sub-text-description text-sm text-sm pointer" : "product-sub-text-description text-sm text-sm"} onClick={() => onCardClick()} >
								<span>{props.data.description} <a href={props.data.storeUrl} className="read-more" tabIndex="0" rel="noopener noreferrer" target="_blank">Read more</a></span>
							</div>
							<div>
								<div className="product-price d-flex mt-3">
									{/* <h5>{props.data.price ? '$ ' + props.data.price : (props.data.isMaItem && props.data.maItemVariantItemRecommendation.length) ? '$ ' + props.data.maItemVariantItemRecommendation[0].price : null}</h5> */}
									<h5>{props.activeVariant?.activeSizeVariant ? '$' + props.activeVariant?.activeSizeVariant.price : '$' + props.data.price}</h5>
									{props.data.strikedPrice ? <span className="strike-price ml-1">{props.data.strikedPrice}</span> : null}
								</div>
								<div className="d-flex align-items-center mt-3">
									<Counter
										counter={props.data.quantity}
										incrementHandler={props.incrementQuantityHandler}
										decrementHandler={props.decrementQuantityHandler} />
									<button className="add-btn ml-auto" onClick={() => props.addProductHandler(props.data)} />
								</div>
								{props.data.maItemVariantItemRecommendation.length ?
									<div className="mt-3 product-variant">
										<div className="mt-3">
											<p className="font-medium product-variant-text">Color</p>
											<div className="d-flex flex-wrap">
												{props.data.maItemVariantItemRecommendation.map(variant =>
													<div
														className={
															["mr-1 mb-1 product-variant-circle-disk", 
															variant.isAvailable ? 'product-variant-avalible' : 'product-variant-disabled',
															(props.activeVariant && props.activeVariant.variantId === variant.variantId) ? "active-variant-product" : ""
														].join(" ")}
														key={variant.variantId + ':' + variant.sku}
														onClick={variant.isAvailable ? () => props.setActiveRecommandationVariantHandler(variant) : () => {}}
													>
														<img className="product-variant-image" src={variant.iconUrl}
															onError={(ev) => onImgLoadError(ev, variant.imageUrl)}
															alt={variant.pattern} />
													</div>
												)}
											</div>
											{props.activeVariant?.activeSizeVariant?.size ?
												<div className="mt-2 size-section" onClick={props.getAllVarient}>
													<Dropdown
														title="Size"
														list={props.activeVariant.allSizeVariant}
														resetThenSet={resetThenSet}
														value={props.activeVariant.activeSizeVariant.size}
													/>
												</div> : null}
										</div>
									</div> : null}
							</div>
						</div>
					</div>
					{openModal ?
						<VariantsModal
							show={openModal}
							data={props.data}
							productDataUpdate={(data) => productDataUpdate(data)}
							closeModal={() => closeCardModal()}
							getAllVarient={() => props.getAllVarient()}
							activeVariant={props.activeVariant}
							incrementQuantityHandler={props.incrementQuantityHandler}
							decrementQuantityHandler={props.decrementQuantityHandler}
							addProductHandler={() => addProductToRegistry()}
							mostWanted={() => setMostWanted()}
							groupedGift={() => setGroupedGift()}
							setActiveVariantSize={(id) => props.setActiveVariantSize(id)}
							setActiveRecommandationVariantHandler={(variant) => props.setActiveRecommandationVariantHandler(variant)}
						/> : null}
				</> : null}
		</>
	)
}

export default CardItem;